import PropTypes from 'prop-types';

export const CommonPropTypes = {
  loading: PropTypes.bool,
  color: PropTypes.string,
  size: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number
  ]),
  margin: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number
  ]),
  animationName: PropTypes.object
};

export const isStringProps = PropTypes.string;
export const isNumberProps = PropTypes.number;
export const isCssValue = PropTypes.oneOfType([ PropTypes.string, PropTypes.number ]);



