import React, { Component } from 'react';
import enhanceLoading from './enhanceLoading';
import { CommonPropTypes, isStringProps } from './propTypes';


const rotateAnimation = {
  '100%': {
    transform: 'rotate(360deg)'
  }
};

const bounceAnimation = {
  '0%, 100%': {
    transform: 'scale(0)'
  },
  '50%': {
    transform: 'scale(1.0)'
  }
};

class Loader extends Component {

  static propTypes = {
    ...CommonPropTypes,
    verticalAlign: isStringProps
  };

  static defaultProps = {
    loading: true,
    color: '#ffffff',
    size: '60px'
  };

  getBallStyle(size) {
    const { color, verticalAlign } = this.props;
    return {
      backgroundColor: color,
      width: size,
      height: size,
      borderRadius: '100%',
      verticalAlign
    };
  }

  getAnimationStyle(index) {
    const { animationName } = this.props;
    const { rotateAnimationKey, bounceAnimationKey } = animationName;
    const animation = [ index === 0 ? rotateAnimationKey : bounceAnimationKey, '2s', index === 2 ? '-1s' : '0s', 'infinite', 'linear' ].join(' ');
    const animationFillMode = 'forwards';

    return {
      animation,
      animationFillMode
    };
  }

  getStyle(index) {
    const { assign } = this.props;
    const size = parseInt(this.props.size);
    const ballSize = size / 2;

    if (index) {
      return assign(this.getBallStyle(ballSize), this.getAnimationStyle(index),
        {
          position: 'absolute',
          top: index % 2 ? 0 : 'auto',
          bottom: index % 2 ? 'auto' : 0
        });
    }

    return assign(this.getAnimationStyle(index),
      {
        width: size,
        height: size,
        position: 'relative'
      });
  }

  render() {
    const { loading, id, className } = this.props;
    if (loading) {
      return (
        <div id={id} className={className}>
          <div style={this.getStyle(0)}>
            <div style={this.getStyle(1)} />
            <div style={this.getStyle(2)} />
          </div>
        </div>
      );
    }

    return null;
  }
}

export default enhanceLoading(Loader, { rotateAnimation, bounceAnimation });



