const property = function(key) {
  return function(obj) {
    return obj == null ? void 0 : obj[key];
  };
};
const MAX_ARRAY_INDEX = Math.pow(2, 53) - 1;
const getLength = property('length');

export const isArray = Array.isArray || function(val){
  return Object.prototype.toString.call(val) === '[object Array]';
};

export const isArrayLike = function(collection) {
  var length = getLength(collection);
  return typeof length == 'number' && length >= 0 && length <= MAX_ARRAY_INDEX;
};

export const isString = function(obj) {
  return Object.prototype.toString.call(obj) === '[object String]';
};

// Is a given variable an object?
export const isObject = function(obj) {
  var type = typeof obj;
  return type === 'function' || type === 'object' && !!obj;
};

export const isArguments = function(obj) {
  return Object.prototype.toString.call(obj) === '[object Arguments]';
};

// Shortcut function for checking if an object has a given property directly
// on itself (in other words, not on a prototype).
export const has = function(obj, path) {
  if (!isArray(path)) {
    return obj != null && Object.prototype.hasOwnProperty.call(obj, path);
  }
  const length = path.length;
  for (let i = 0; i < length; i++) {
    const key = path[i];
    if (obj == null || !Object.prototype.hasOwnProperty.call(obj, key)) {
      return false;
    }
    obj = obj[key];
  }
  return !!length;
};

export const keys = function(obj) {
  if (!isObject(obj)) {
    return [];
  }
  if (Object.keys) {
    return Object.keys(obj);
  }
  const keys = [];
  for (let key in obj) {
    if (has(obj, key)) {
      keys.push(key);
    }
  }
  return keys;
};

// Is a given array, string, or object empty?
// An "empty" object has no enumerable own-properties.
export const isEmpty = function(objOrArray) {
  if (objOrArray == null) {
    return true;
  }
  if (isArrayLike(objOrArray) && (isArray(objOrArray) || isString(objOrArray) || isArguments(objOrArray))) {
    return objOrArray.length === 0;
  }
  return keys(objOrArray).length === 0;
};




