import React, { Component } from 'react';
import enhanceLoading from './enhanceLoading';
import { CommonPropTypes, isStringProps } from './propTypes';

var commonAnimation = {
  '0%': {
    transform: 'rotate(0deg) scale(1)'
  },
  '50%': {
    transform: 'rotate(180deg) scale(0.8)'
  },
  '100%': {
    transform: 'rotate(360deg) scale(1)'
  }
};

class Loader extends Component {

  static propTypes = {
    ...CommonPropTypes,
    verticalAlign: isStringProps
  };

  static defaultProps = {
    loading: true,
    color: '#ffffff',
    size: '35px'
  };

  getBallStyle() {
    const { color, size, verticalAlign } = this.props;
    return {
      width: size,
      height: size,
      border: '2px solid',
      borderColor: color,
      borderBottomColor: 'transparent',
      borderRadius: '100%',
      background: 'transparent !important',
      verticalAlign
    };
  }

  getAnimationStyle(index) {
    const { animationName } = this.props;
    const { commonAnimationKey } = animationName;
    const animation = [ commonAnimationKey, '0.75s', '0s', 'infinite', 'linear' ].join(' ');
    const animationFillMode = 'both';

    return {
      animation,
      animationFillMode
    };
  }

  getStyle(index) {
    const { assign } = this.props;
    return assign(this.getBallStyle(index), this.getAnimationStyle(index), { display: 'inline-block' });
  }

  render() {
    const { loading, id, className } = this.props;
    if (loading) {
      return (
        <div id={id} className={className}>
          <div style={this.getStyle()} />
        </div>
      );
    }

    return null;
  }
}

export default enhanceLoading(Loader, { commonAnimation });



