import React, { Component } from 'react';
import enhanceLoading from './enhanceLoading';
import { CommonPropTypes, isStringProps } from './propTypes';

const riseAmount = 30;

const evenAnimation = {
  '0%': {
    transform: 'scale(1.1)'
  },
  '25': {
    transform: `translateY(-${riseAmount}px)`
  },
  '50%': {
    transform: 'scale(0.4)'
  },
  '75%': {
    transform: `translateY(${riseAmount}px)`
  },
  '100%': {
    transform: 'translateY(0) scale(1.0)'
  }
};

const oddAnimation = {
  '0%': {
    transform: 'scale(0.4)'
  },
  '25': {
    transform: `translateY(${riseAmount}px)`
  },
  '50%': {
    transform: 'scale(1.1)'
  },
  '75%': {
    transform: `translateY(-${riseAmount}px)`
  },
  '100%': {
    transform: 'translateY(0) scale(0.75)'
  }
};

class Loader extends Component {
  static propTypes = {
    ...CommonPropTypes,
    verticalAlign: isStringProps
  };

  static defaultProps = {
    loading: true,
    color: '#ffffff',
    size: '15px',
    margin: '2px'
  };

  getBallStyle() {
    return {
      backgroundColor: this.props.color,
      width: this.props.size,
      height: this.props.size,
      margin: this.props.margin,
      borderRadius: '100%',
      verticalAlign: this.props.verticalAlign
    };
  }

  getAnimationStyle(index) {
    const { animationName } = this.props;
    const { evenAnimationKey, oddAnimationKey } = animationName;
    const animation = [ index % 2 === 0 ? evenAnimationKey : oddAnimationKey, '1s', '0s', 'infinite', 'cubic-bezier(.15,.46,.9,.6)' ].join(' ');
    const animationFillMode = 'both';

    return {
      animation,
      animationFillMode
    };
  }

  getStyle(index) {
    const { assign } = this.props;
    return assign(
            this.getBallStyle(index),
            this.getAnimationStyle(index),
      {
        display: 'inline-block'
      }
        );
  }

  render() {
    const { loading, id, className } = this.props;
    if (loading) {
      return (
        <div id={id} className={className}>
          <div style={this.getStyle(1)} />
          <div style={this.getStyle(2)} />
          <div style={this.getStyle(3)} />
          <div style={this.getStyle(4)} />
          <div style={this.getStyle(5)} />
        </div>
      );
    }

    return null;
  }
}

export default enhanceLoading(Loader, { evenAnimation, oddAnimation });



