import React, { Component } from 'react';
import enhanceLoading from './enhanceLoading';
import { CommonPropTypes, isStringProps } from './propTypes';

const commonAnimation = {
  '100%': {
    transform: 'rotate(360deg)'
  }
};

class Loader extends Component {
  static propTypes = {
    ...CommonPropTypes,
    verticalAlign: isStringProps
  };

  static defaultProps = {
    loading: true,
    color: '#ffffff',
    size: '60px'
  };

  getBallStyle(size) {
    return {
      width: size,
      height: size,
      borderRadius: '100%',
      verticalAlign: this.props.verticalAlign
    };
  }

  getAnimationStyle(index) {
    const { animationName } = this.props;
    const { commonAnimationKey } = animationName;
    const animation = [ commonAnimationKey, '0.6s', '0s', 'infinite', 'linear' ].join(' ');
    const animationFillMode = 'forwards';

    return {
      animation,
      animationFillMode
    };
  }

  getStyle(index) {
    const { assign } = this.props;
    const size = parseInt(this.props.size);
    const moonSize = size / 7;

    if (index === 1) {
      return assign(
                this.getBallStyle(moonSize),
                this.getAnimationStyle(index),
        {
          backgroundColor: this.props.color,
          opacity: '0.8',
          position: 'absolute',
          top: size / 2 - moonSize / 2
        }
            );
    }
    else if (index === 2) {
      return assign(
                this.getBallStyle(size),
        {
          border: `${moonSize}px solid ${this.props.color}`,
          opacity: 0.1
        }
            );
    }
    else {
      return assign(
                this.getAnimationStyle(index),
        {
          position: 'relative'
        }
            );
    }
  }

  render() {
    const { loading, id, className } = this.props;
    if (loading) {
      return (
        <div id={id} className={className}>
          <div style={this.getStyle(0)}>
            <div style={this.getStyle(1)} />
            <div style={this.getStyle(2)} />
          </div>
        </div>
      );
    }

    return null;
  }
}

export default enhanceLoading(Loader, { commonAnimation });



