import React, { Component } from 'react';
import enhanceLoading from './enhanceLoading';
import { CommonPropTypes, isStringProps } from './propTypes';

const commonAnimation = {
  '0%': {
    transform: 'scale(1)'
  },
  '50%': {
    transform: 'scale(0.5)',
    opacity: 0.7
  },
  '100%': {
    transform: 'scale(1)',
    opacity: 1
  }
};

function random(top) {
  return Math.random() * top;
}

class Loader extends Component {
  static propTypes = {
    ...CommonPropTypes,
    verticalAlign: isStringProps
  };

  static defaultProps = {
    loading: true,
    color: '#ffffff',
    size: '15px',
    margin: '2px'
  };

  getBallStyle() {
    return {
      backgroundColor: this.props.color,
      width: this.props.size,
      height: this.props.size,
      margin: this.props.margin,
      borderRadius: '100%',
      verticalAlign: this.props.verticalAlign
    };
  }

  getAnimationStyle(index) {
    const { animationName } = this.props;
    const { commonAnimationKey } = animationName;

    const animationDuration = `${(random(100) / 100) + 0.6}s`;
    const animationDelay = `${(random(100) / 100) - 0.2}s`;

    const animation = [ commonAnimationKey, animationDuration, animationDelay, 'infinite', 'ease' ].join(' ');
    const animationFillMode = 'both';

    return {
      animation,
      animationFillMode
    };
  }

  getStyle(index) {
    const { assign } = this.props;
    return assign(
            this.getBallStyle(index),
            this.getAnimationStyle(index),
      {
        display: 'inline-block'
      }
        );
  }

  render() {
    const { loading, id, className } = this.props;
    if (loading) {
      var style = {
        width: (parseFloat(this.props.size) * 3) + parseFloat(this.props.margin) * 6,
        fontSize: 0
      };

      return (
        <div id={id} className={className}>
          <div style={style}>
            <div style={this.getStyle(1)} />
            <div style={this.getStyle(2)} />
            <div style={this.getStyle(3)} />
            <div style={this.getStyle(4)} />
            <div style={this.getStyle(5)} />
            <div style={this.getStyle(6)} />
            <div style={this.getStyle(7)} />
            <div style={this.getStyle(8)} />
            <div style={this.getStyle(9)} />
          </div>
        </div>
      );
    }

    return null;
  }
}

export default enhanceLoading(Loader, { commonAnimation });



