import React, { Component } from 'react';
import enhanceLoading from './enhanceLoading';
import { CommonPropTypes, isStringProps } from './propTypes';

const commonAnimation = {
  '50%': {
    transform: 'scale(0.75)',
    opacity: 0.2
  },
  '100%': {
    transform: 'scale(1)',
    opacity: 1
  }
};

class Loader extends Component {

  static propTypes = {
    ...CommonPropTypes,
    verticalAlign: isStringProps
  };

  static defaultProps = {
    loading: true,
    color: '#ffffff',
    size: '15px',
    margin: '2px',
    animationName: {}
  };

  getBallStyle() {
    const { color, size, margin, verticalAlign } = this.props;
    return {
      backgroundColor: color,
      width: size,
      height: size,
      margin,
      borderRadius: '100%',
      verticalAlign
    };
  }

  getAnimationStyle(index) {
    const { animationName } = this.props;
    const { commonAnimationKey } = animationName;
    const animation = [ commonAnimationKey, '0.7s', index % 2 ? '0s' : '0.35s', 'infinite', 'linear' ].join(' ');
    const animationFillMode = 'both';

    return {
      animation,
      animationFillMode
    };
  }

  getStyle(index) {
    const { assign } = this.props;
    return assign(this.getBallStyle(index), this.getAnimationStyle(index), { display: 'inline-block' });
  }

  render() {
    const { loading, id, className } = this.props;
    if (loading) {
      return (
        <div id={id} className={className}>
          <div style={this.getStyle(1)} />
          <div style={this.getStyle(2)} />
          <div style={this.getStyle(3)} />
        </div>
      );
    }
    return null;
  }
}

export default enhanceLoading(Loader, { commonAnimation });



