import React, { Component } from 'react';
import enhanceLoading from './enhanceLoading';
import { CommonPropTypes, isStringProps } from './propTypes';

const animations = {};

class Loader extends Component {
  static propTypes = {
    ...CommonPropTypes,
    verticalAlign: isStringProps
  };

  static defaultProps = {
    loading: true,
    color: '#ffffff',
    size: 25,
    margin: 2
  };

  getBallStyle() {
    return {
      backgroundColor: this.props.color,
      width: this.props.size,
      height: this.props.size,
      margin: this.props.margin,
      borderRadius: '100%',
      verticalAlign: this.props.verticalAlign
    };
  }

  getAnimationStyle(index) {
    const { insertKeyframesRule } = this.props;
    const size = this.props.size;
    let animationName = animations[size];

    if (!animationName) {
      const commonAnimation = {
        '75%': {
          opacity: 0.7
        },
        '100%': {
          transform: `translate(${-4 * size}px,${-size / 4}px)`
        }
      };
      animationName = animations[size] = insertKeyframesRule(commonAnimation);
    }

    const animation = [ animationName, '1s', `${index * 0.25}s`, 'infinite', 'linear' ].join(' ');
    const animationFillMode = 'both';

    return {
      animation,
      animationFillMode
    };
  }

  getStyle(index) {
    const { assign } = this.props;
    if (index === 1) {
      const s1 =  `${this.props.size}px solid transparent`;
      const s2 =  `${this.props.size}px solid ${this.props.color}`;

      return {
        width: 0,
        height: 0,
        borderRight: s1,
        borderTop: s2,
        borderLeft: s2,
        borderBottom: s2,
        borderRadius: this.props.size
      };
    }

    return assign(
            this.getBallStyle(index),
            this.getAnimationStyle(index),
      {
        width: 10,
        height: 10,
        transform: `translate(0, ${-this.props.size / 4}px)`,
        position: 'absolute',
        top: 25,
        left: 100
      }
        );
  }

  render() {
    const { loading, id, className } = this.props;

    if (loading) {
      const style = {
        position: 'relative',
        fontSize: 0
      };

      return (
        <div id={id} className={className}>
          <div style={style}>
            <div style={this.getStyle(1)} />
            <div style={this.getStyle(2)} />
            <div style={this.getStyle(3)} />
            <div style={this.getStyle(4)} />
            <div style={this.getStyle(5)} />
          </div>
        </div>
      );
    }

    return null;
  }
}

export default enhanceLoading(Loader);



