
import React, { Component } from 'react';
import enhanceLoading from './enhanceLoading';
import { CommonPropTypes, isStringProps } from './propTypes';

const commonAnimation = {
  '0%, 100%': {
    transform: 'scale(0)'
  },
  '50%': {
    transform: 'scale(1.0)'
  }
};

class Loader extends Component {

  static propTypes = {
    ...CommonPropTypes,
    verticalAlign: isStringProps
  };

  static defaultProps = {
    loading: true,
    color: '#ffffff',
    size: '60px'
  };

  getBallStyle() {
    const { color, size, verticalAlign } = this.props;
    return {
      backgroundColor: color,
      width: size,
      height: size,
      borderRadius: '100%',
      opacity: 0.6,
      position: 'absolute',
      top: 0,
      left: 0,
      verticalAlign
    };
  }

  getAnimationStyle(index) {
    const { animationName } = this.props;
    const { commonAnimationKey } = animationName;
    const animation = [ commonAnimationKey, '2s', index === 1 ? '1s' : '0s', 'infinite', 'ease-in-out' ].join(' ');
    const animationFillMode = 'both';

    return {
      animation,
      animationFillMode
    };
  }

  getStyle(index) {
    const { assign } = this.props;

    if (index) {
      return assign(this.getBallStyle(index), this.getAnimationStyle(index));
    }
    return assign(
      {
        width: this.props.size,
        height: this.props.size,
        position: 'relative'
      });
  }

  render() {
    const { loading, id, className } = this.props;
    if (loading) {
      return (
        <div id={id} className={className}>
          <div style={this.getStyle()}>
            <div style={this.getStyle(1)} />
            <div style={this.getStyle(2)} />
          </div>
        </div>
      );
    }
    return null;
  }
}

export default enhanceLoading(Loader, { commonAnimation });



