import React, { Component } from 'react';
import enhanceLoading from './enhanceLoading';
import { CommonPropTypes, isStringProps } from './propTypes';

const commonAnimation = {
  '25%': {
    transform: 'rotateX(180deg) rotateY(0)'
  },
  '50%': {
    transform: 'rotateX(180deg) rotateY(180deg)'
  },
  '75%': {
    transform: 'rotateX(0) rotateY(180deg)'
  },
  '100%': {
    transform: 'rotateX(0) rotateY(0)'
  }
};

class Loader extends Component {
  static propTypes = {
    ...CommonPropTypes,
    verticalAlign: isStringProps
  };

  static defaultProps = {
    loading: true,
    color: '#ffffff',
    size: '50px'
  };

  getSquareStyle() {
    return {
      backgroundColor: this.props.color,
      width: this.props.size,
      height: this.props.size,
      verticalAlign: this.props.verticalAlign
    };
  }

  getAnimationStyle(index) {
    const { animationName } = this.props;
    const { commonAnimationKey } = animationName;
    const animation = [ commonAnimationKey, '3s', '0s', 'infinite', 'cubic-bezier(.09,.57,.49,.9)' ].join(' ');
    const animationFillMode = 'both';
    const perspective = '100px';

    return {
      perspective,
      animation,
      animationFillMode
    };
  }

  getStyle(index) {
    const { assign } = this.props;
    return assign(
            this.getSquareStyle(index),
            this.getAnimationStyle(index),
      {
        display: 'inline-block'
      }
        );
  }

  render() {
    const { loading, id, className } = this.props;
    if (loading) {
      return (
        <div id={id} className={className}>
          <div style={this.getStyle()} />
        </div>
      );
    }

    return null;
  }
}

export default enhanceLoading(Loader, { commonAnimation });



