import React, { Component } from 'react';
import enhanceLoading from './enhanceLoading';
import { CommonPropTypes, isStringProps, isCssValue } from './propTypes';

const commonAnimation = {
  '50%': {
    opacity: 0.3
  },
  '100%': {
    opacity: 1
  }
};


class Loader extends Component {
  static propTypes = {
    ...CommonPropTypes,
    verticalAlign: isStringProps,
    height: isCssValue,
    width: isCssValue,
    radius: isCssValue
  };

  static defaultProps = {
    loading: true,
    color: '#ffffff',
    height: '15px',
    width: '5px',
    margin: '2px',
    radius: '2px'
  };

  getLineStyle(index) {
    return {
      backgroundColor: this.props.color,
      height: this.props.height,
      width: this.props.width,
      margin: this.props.margin,
      borderRadius: this.props.radius,
      verticalAlign: this.props.verticalAlign
    };
  }

  getAnimationStyle(index) {
    const { animationName } = this.props;
    const { commonAnimationKey } = animationName;
    const animation = [ commonAnimationKey, '1.2s', `${index * 0.12}s`, 'infinite', 'ease-in-out' ].join(' ');
    const animationFillMode = 'both';

    return {
      animation,
      animationFillMode
    };
  }

  getPosStyle(index) {
    const radius = 20;
    const quarter = (radius / 2) + (radius / 5.5);

    const lines = {
      l1: {
        top: radius,
        left: 0
      },
      l2: {
        top: quarter,
        left: quarter,
        transform: 'rotate(-45deg)'
      },
      l3: {
        top: 0,
        left: radius,
        transform: 'rotate(90deg)'
      },
      l4: {
        top: -quarter,
        left: quarter,
        transform: 'rotate(45deg)'
      },
      l5: {
        top: -radius,
        left: 0
      },
      l6: {
        top: -quarter,
        left: -quarter,
        transform: 'rotate(-45deg)'
      },
      l7: {
        top: 0,
        left: -radius,
        transform: 'rotate(90deg)'
      },
      l8: {
        top: quarter,
        left: -quarter,
        transform: 'rotate(45deg)'
      }
    };

    return lines[`l${index}`];
  }

  getStyle(index) {
    const { assign } = this.props;
    return assign(
            this.getLineStyle(index),
            this.getPosStyle(index),
            this.getAnimationStyle(index),
      {
        position: 'absolute'
      }
        );
  }

  render() {
    const { loading, id, className } = this.props;
    if (loading) {
      var style = {
        position: 'relative',
        fontSize: 0
      };

      return (
        <div id={id} className={className}>
          <div style={style}>
            <div style={this.getStyle(1)} />
            <div style={this.getStyle(2)} />
            <div style={this.getStyle(3)} />
            <div style={this.getStyle(4)} />
            <div style={this.getStyle(5)} />
            <div style={this.getStyle(6)} />
            <div style={this.getStyle(7)} />
            <div style={this.getStyle(8)} />
          </div>
        </div>
      );
    }

    return null;
  }
}

export default enhanceLoading(Loader, { commonAnimation });



