import React, { Component } from 'react';
import enhanceLoading from './enhanceLoading';
import { CommonPropTypes, isStringProps } from './propTypes';

const rightRotateAnimation = {
  '0%': {
    transform: 'rotateX(0deg) rotateY(0deg) rotateZ(0deg)'
  },
  '100%': {
    transform: 'rotateX(180deg) rotateY(360deg) rotateZ(360deg)'
  }
};

const leftRotateAnimation = {
  '0%': {
    transform: 'rotateX(0deg) rotateY(0deg) rotateZ(0deg)'
  },
  '100%': {
    transform: 'rotateX(360deg) rotateY(180deg) rotateZ(360deg)'
  }
};

class Loader extends Component {
  static propTypes = {
    ...CommonPropTypes,
    verticalAlign: isStringProps
  };

  static defaultProps = {
    loading: true,
    color: '#ffffff',
    size: '60px'
  };

  getCircleStyle(size) {
    return {
      width: size,
      height: size,
      border: `${size / 10}px solid ${this.props.color}`,
      opacity: 0.4,
      borderRadius: '100%',
      verticalAlign: this.props.verticalAlign
    };
  }

  getAnimationStyle(index) {
    const { animationName } = this.props;
    const { rightRotateAnimationKey, leftRotateAnimationKey } = animationName;
    const animation = [ index === 1 ? rightRotateAnimationKey : leftRotateAnimationKey, '2s', '0s', 'infinite', 'linear' ].join(' ');
    const animationFillMode = 'forwards';
    const perspective = '800px';

    return {
      perspective,
      animation,
      animationFillMode
    };
  }

  getStyle(index) {
    const { assign } = this.props;
    var size = parseInt(this.props.size);

    if (index) {
      return assign(
                this.getCircleStyle(size),
                this.getAnimationStyle(index),
        {
          position: 'absolute',
          top: 0,
          left: 0
        }
            );
    }

    return {
      width: size,
      height: size,
      position: 'relative'
    };
  }

  render() {
    const { loading, id, className } = this.props;
    if (loading) {
      return (
        <div id={id} className={className}>
          <div style={this.getStyle(0)}>
            <div style={this.getStyle(1)} />
            <div style={this.getStyle(2)} />
          </div>
        </div>
      );
    }

    return null;
  }
}

export default enhanceLoading(Loader, { rightRotateAnimation, leftRotateAnimation });



