import React, { Component } from 'react';
import { appendVendorPrefix as assign, insertKeyframesRule } from 'domtool';
import { keys, isEmpty } from './_utils';

export default function enhanceLoading(WrapperComponent, keyframes = {}){

  const animationName = {};
  if (!isEmpty(keyframes)){
    const _keys = keys(keyframes);
    _keys.forEach(kf => animationName[`${kf}Key`] = insertKeyframesRule(keyframes[kf]));
  }

  class Loading extends Component {
    constructor(props) {
      super(props);

    }

    render() {
      return (
        <WrapperComponent
          animationName={animationName}
          assign={assign}
          insertKeyframesRule={insertKeyframesRule}
          {...this.props}
        />
      );
    }

  }

  return Loading;
}



