import React, { Component } from 'react';
import { render } from 'react-dom';
import RayLoading from 'rayloading';

import './app.scss';

class Example extends Component {

  constructor(props) {
    super(props);
    this.state = {
      color: RayLoading.colors.dotred
    };
  }

  render() {

    const { color } = this.state;

    const style = {
      display: 'flex',
      WebkitFlex: '0 1 auto',
      flex: '0 1 auto',
      WebkitFlexDirection: 'column',
      flexDirection: 'column',
      WebkitFlexGrow: 1,
      flexGrow: 1,
      WebkitFlexShrink: 0,
      flexShrink: 0,
      WebkitFlexBasis: '25%',
      flexBasis: '25%',
      maxWidth: '25%',
      height: '200px',
      WebkitAlignItems: 'center',
      alignItems: 'center',
      WebkitJustifyContent: 'center',
      justifyContent: 'center'
    };

    return (
      <div className="ray-loading">
        <header className="row">
          <div className="container">
            <h1 className="logo">RayLoading</h1>
          </div>
        </header>
        <div className="center">
          <code className="installer">npm install halogen</code>
        </div>
        <div
          className="example"
          style={{
            boxSizing: 'border-box',
            display: 'flex',
            WebkitFlex: '0 1 auto',
            flex: '0 1 auto',
            WebkitFlexDirection: 'row',
            flexDirection: 'row',
            WebkitFlexWrap: 'wrap',
            flexWrap: 'wrap'
          }}
        >
          <div style={style}>PulseLoader<RayLoading.PulseLoader color={color} /></div>
          <div style={style}>GridLoader<RayLoading.GridLoader color={color} /></div>
          <div style={style}>ClipLoader<RayLoading.ClipLoader color={color} /></div>
          <div style={style}>RiseLoader<RayLoading.RiseLoader color={color} /></div>
          <div style={style}>BeatLoader<RayLoading.BeatLoader color={color} /></div>
          <div style={style}>SyncLoader<RayLoading.SyncLoader color={color} /></div>
          <div style={style}>RotateLoader<RayLoading.RotateLoader color={color} /></div>
          <div style={style}>FadeLoader<RayLoading.FadeLoader color={color} /></div>
          <div style={style}>PacmanLoader<RayLoading.PacmanLoader color={color} /></div>
          <div style={style}>SquareLoader<RayLoading.SquareLoader color={color} /></div>
          <div style={style}>ScaleLoader<RayLoading.ScaleLoader color={color} /></div>
          <div style={style}>SkewLoader<RayLoading.SkewLoader color={color} /></div>
          <div style={style}>MoonLoader<RayLoading.MoonLoader color={color} /></div>
          <div style={style}>RingLoader<RayLoading.RingLoader color={color} /></div>
          <div style={style}>BounceLoader<RayLoading.BounceLoader color={color} /></div>
          <div style={style}>DotLoader<RayLoading.DotLoader color={color} /></div>
        </div>
      </div>
    );
  }
}

render(<Example />, document.getElementById('__example__'));



